import React, { createContext, useReducer } from "react";
import { BuildByType, BuildJobType } from "../constants/BuildJobInfo";
import { InstallerType } from "../constants/InstallerType";
import { restReducer } from "../reducers/restReducer";

const initialState = {
	logs: [],
	uiSettings: {
		getconfig: "r01",
		compilemode: "",
		cylancedesktopfolder: "C:\\Program Files\\Cylance\\Desktop",
		gettoken: "DSDDE1OVnh1KK7k6D0Ibgm8U",
		buildJobSourceInfo: {
			buildJobSourceType: BuildJobType.Me,
			buildJobUrl: "TestJobs/job/jshih/job/agent-p-publish-install-test",
			releaseVersion: "3.1",
		},
		buildJobTypeInfo: {
			buildType: BuildByType.Latest.value,
			buildVersion: "",
			buildNumber: "",
		},
		installerType: InstallerType.Msi,
	},
	reports: [],
};

export const CommandsContext = createContext(initialState);

const CommandsContextProvider = (props) => {
	const [state, dispatch] = useReducer(restReducer, initialState);

	return (
		<CommandsContext.Provider value={{ state, dispatch }}>
			{props.children}
		</CommandsContext.Provider>
	);
};

export default CommandsContextProvider;
