import {
	Box,
	FormControl,
	FormControlLabel,
	MenuItem,
	Radio,
	RadioGroup,
	Select,
	TextField,
} from "@mui/material";
import React, { useContext, useState } from "react";
import { BuildByType } from "../constants/BuildJobInfo";
import ReducerAction from "../constants/ReducerAction";
import { CommandsContext } from "../contexts/CommandsContext";

const BuildType = () => {
	const {
		state: { uiSettings },
		dispatch,
	} = useContext(CommandsContext);
	const [buildType, setBuildType] = useState(
		uiSettings.buildJobTypeInfo.buildType
	);
	const [buildVersion, setBuildVersion] = useState(
		uiSettings.buildJobTypeInfo.buildVersion
	);
	const [buildNumber, setBuildNumber] = useState(
		uiSettings.buildJobTypeInfo.buildNumber
	);

	const [invalidVersion, setInvalidVersion] = useState(false);

	const handleBuildTypeChange = (e) => {
		setBuildType(e.target.value);

		let version = "";
		let buildNo = "";

		switch (e.target.value) {
			case BuildByType.Latest.value:
				buildNo = "lastSuccessfulBuild";
				break;
			case BuildByType.Version.value:
				version = buildVersion;
				break;
			case BuildByType.BuildNumber.value:
				buildNo = buildNumber;
				break;
			default:
		}

		dispatch({
			type: ReducerAction.UpdateUiSettings,
			payload: {
				...uiSettings,
				buildJobTypeInfo: {
					buildType: buildType,
					buildVersion: version,
					buildNumber: buildNo,
				},
			},
		});
	};
	const handleVersionChange = (e) => {
		// Regular expression that controls input during typing which allows only numbers and period.
		const regex = /^[1-9]\d*(\.[1-9]\d*)*$/;

		// Regular expression that controls the final input, which is in x.x.x.x format.
		const regexFinal =
			/^(0|[1-9]\d*)\.(0|[1-9]\d*)\.(0|[1-9]\d*)\.(0|[1-9]\d*)$/;

		// If the regex validation fails becuase a period is being entered,
		// do not fail the validation.
		const isValidVersion =
			e.target.value === "" || regexFinal.test(e.target.value);

		if (!isValidVersion) {
			let version = e.target.value;

			if (!regex.test(version)) {
				if (version.substring(version.length - 1) !== ".") return;

				version = version.substring(0, version.length - 1);
				if (!regex.test(version)) return;

				setInvalidVersion(true);
			} else {
				setInvalidVersion(false);
			}

			console.log("Invalid version", e.target.value);
			setBuildVersion(e.target.value);
			return;
		}

		// Only write to  uiSettings when the version is valid.
		console.log("Valid version", e.target.value);
		setInvalidVersion(false);
		setBuildType(BuildByType.Version.value);
		setBuildVersion(e.target.value);
		dispatch({
			type: ReducerAction.UpdateUiSettings,
			payload: {
				...uiSettings,
				buildJobTypeInfo: {
					buildType: BuildByType.Version.value,
					buildVersion: e.target.value,
				},
			},
		});
	};

	const handleBuildNumberChange = (e) => {
		const regex = /^[0-9\b]+$/;
		// console.log("regex.test(e.target.value)", e.target.value);

		if (e.target.value === "" || regex.test(e.target.value)) {
			setBuildNumber(e.target.value);
			setBuildType(BuildByType.BuildNumber.value);
			// console.log(uiSettings);
			dispatch({
				type: ReducerAction.UpdateUiSettings,
				payload: {
					...uiSettings,
					buildJobTypeInfo: {
						buildType: BuildByType.BuildNumber.value,
						buildNumber: e.target.value,
					},
				},
			});
		}
	};

	return (
		<Box sx={{ textAlign: "left", fontSize: 12 }}>
			<Box>Build Type</Box>
			<FormControl>
				<RadioGroup
					value={buildType}
					onChange={(e) => handleBuildTypeChange(e)}
				>
					<FormControlLabel
						key={BuildByType.Latest.value}
						value={BuildByType.Latest.value}
						control={<Radio size="small" sx={{ ml: 2, pt: 0, pb: 0 }} />}
						label={<Box fontSize={12}>{BuildByType.Latest.name}</Box>}
					/>
					<Box display="flex">
						<FormControlLabel
							key={BuildByType.Version.value}
							value={BuildByType.Version.value}
							control={<Radio size="small" sx={{ ml: 2, pt: 0, pb: 0 }} />}
							label={<Box fontSize={12}>{BuildByType.Version.name}</Box>}
						/>
						<TextField
							error={invalidVersion}
							label={invalidVersion ? "Invalid version" : ""}
							size="small"
							inputProps={{
								maxLength: 15,
								style: { fontSize: 12, padding: 5 },
							}}
							sx={{
								width: { sm: 115 },
								"& .MuiInputBase-root": { height: 25 },
							}}
							onChange={(e) => handleVersionChange(e)}
							value={buildVersion}
						/>
					</Box>
					<Box display="flex">
						<FormControlLabel
							key={BuildByType.BuildNumber.value}
							value={BuildByType.BuildNumber.value}
							control={<Radio size="small" sx={{ ml: 2, pt: 0, pb: 0 }} />}
							label={<Box fontSize={12}>{BuildByType.BuildNumber.name}</Box>}
						/>
						<TextField
							size="small"
							inputProps={{ maxLength: 6, style: { fontSize: 12, padding: 5 } }}
							sx={{
								width: { sm: 82 },
								"& .MuiInputBase-root": { height: 25 },
							}}
							onChange={(e) => handleBuildNumberChange(e)}
							value={buildNumber}
						/>
					</Box>
				</RadioGroup>
			</FormControl>
		</Box>
	);
};

export default BuildType;
