import {
	FormControl,
	FormControlLabel,
	Radio,
	RadioGroup,
} from "@mui/material";
import { Box } from "@mui/system";
import React, { useContext, useState } from "react";
import { Installers } from "../constants/InstallerType";
import ReducerAction from "../constants/ReducerAction";
import { CommandsContext } from "../contexts/CommandsContext";

const Installer = () => {
	const {
		state: { uiSettings },
		dispatch,
	} = useContext(CommandsContext);

	const [installType, setInstallType] = useState(uiSettings.installerType);

	const handleInstallerTypeChange = (e) => {
		setInstallType(e.target.value);
		dispatch({
			type: ReducerAction.UpdateUiSettings,
			payload: { ...uiSettings, installerType: e.target.value },
		});
	};

	return (
		<Box flex="column" sx={{ textAlign: "left", fontSize: 12 }}>
			<Box>Installer</Box>
			<FormControl>
				<RadioGroup
					row
					sx={{ ml: 2 }}
					value={installType}
					onChange={(e) => handleInstallerTypeChange(e)}
				>
					{Installers.map((i) => (
						<FormControlLabel
							key={i.value}
							value={i.value}
							control={<Radio size="small" sx={{ pt: 0, pb: 0 }} />}
							label={<Box fontSize={12}>{i.name}</Box>}
						/>
					))}
				</RadioGroup>
			</FormControl>
		</Box>
	);
};

export default Installer;
