import { Box, Button, Divider } from "@mui/material";
import React, { useContext } from "react";
import BaseBox from "../components/BaseBox";
import ListHeader from "../components/ListHeader";
import Jenkin from "./Jenkins";
import BuildType from "./BuildType";
import Installer from "./Installer";
import { CommandsContext } from "../contexts/CommandsContext";
import { runRestCommand } from "../data/webCommands";
import URL from "../constants/Url";

const DownloadOptions = () => {
	const {
		state: { uiSettings },
		dispatch,
	} = useContext(CommandsContext);

	const handleClick = () => {
		// console.log(uiSettings)
		runRestCommand(URL.DOWNLOAD_COMMAND, null, null, uiSettings, dispatch);
	};

	return (
		<BaseBox>
			<ListHeader name="Download" />
			<Divider sx={{ mb: 1 }} />
			<Box flex="column">
				<Jenkin />
				<Divider sx={{ mt: 1, mb: 1 }} />
				<BuildType />
				<Divider sx={{ mt: 1, mb: 1 }} />
				<Installer />
				<Divider sx={{ mt: 1, mb: 1 }} />
				<Button variant="contained" sx={{ mb: 1 }} onClick={handleClick}>
					Download
				</Button>
			</Box>
		</BaseBox>
	);
};

export default DownloadOptions;
