import {
	FormControl,
	FormControlLabel,
	MenuItem,
	Radio,
	RadioGroup,
	Select,
} from "@mui/material";
import { Box } from "@mui/system";
import React, { useContext, useState } from "react";
import ReducerAction from "../constants/ReducerAction";
import { CommandsContext } from "../contexts/CommandsContext";
import UiDataSource from "../UiSettings.json";
import { BuildJobType, BuildJobTypeName } from "../constants/BuildJobInfo";

//const releaseVersions = ["3.1", "3.0", "2.1.1580", "2.1.1578"];

const Jenkin = () => {
	const {
		state: { uiSettings },
		dispatch,
	} = useContext(CommandsContext);

	const [buildJobSourceType, setbuildJobSourceType] = useState(
		uiSettings.buildJobSourceInfo.buildJobSourceType
	);

	const [releaseVersion, setReleaseVersion] = useState(
		uiSettings.buildJobSourceInfo.releaseVersion
	);

	const jenkinsUrls = UiDataSource.UiData.JenkinsUrls;
	// console.log("jenkinsUrls", jenkinsUrls);

	const releaseUrls = jenkinsUrls.filter((url) => {
		return url.name !== BuildJobTypeName.Me && url.name !== BuildJobTypeName.BC;
	});
	// console.log(releaseUrls);

	const handleJenkinsChange = (e) => {
		setbuildJobSourceType(e.target.value);
		handleBuildJobChange(e.target.value, releaseVersion);
	};

	const handleReleaseVersionChange = (e) => {
		setReleaseVersion(e.target.value);
		setbuildJobSourceType(BuildJobType.Release);
		handleBuildJobChange(BuildJobType.Release, e.target.value);
	};

	const handleBuildJobChange = (buildJobType, relVersion = null) => {
		console.log("handleBuildJobChange", buildJobType);
		let buildName = "";

		switch (parseInt(buildJobType))
		{
			case BuildJobType.Me:
				buildName = BuildJobTypeName.Me;
				break;
			case BuildJobType.BC:
				buildName = BuildJobTypeName.BC;
				break;
			case BuildJobType.Release:
				buildName = relVersion;
				break;
			default:
				buildName="123"
		}

		// console.log("buildName", buildName)
		dispatch({
			type: ReducerAction.UpdateUiSettings,
			payload: {
				...uiSettings,
				buildJobSourceInfo: {
					buildJobSourceType: buildJobType,
					buildJobUrl: `${getJobUrlByBuildType(buildName)}`,
					releaseVersion:
						buildJobType === BuildJobType.Release ? relVersion : "",
				},
			},
		});
	};

	const getJobUrlByBuildType = (buildTypeName) => {
		const url = jenkinsUrls.find((u) => {
			return u.name === buildTypeName;
		})?.url;
		console.log("getJobUrlByBuildType", buildTypeName, url);
		return url;
	};

	return (
		<Box flex="column" sx={{ textAlign: "left", fontSize: 12 }}>
			Jenkins Job
			<FormControl>
				<RadioGroup
					row
					value={buildJobSourceType}
					onChange={(e) => handleJenkinsChange(e)}
				>
					<Box display="flex" sx={{ ml: 2 }}>
						<FormControlLabel
							key={BuildJobType.Me}
							value={BuildJobType.Me}
							control={<Radio size="small" sx={{ pt: 0, pb: 0 }} />}
							label={<Box fontSize={12}>{BuildJobTypeName.Me}</Box>}
						/>
						<FormControlLabel
							key={BuildJobType.BC}
							value={BuildJobType.BC}
							control={<Radio sx={{ pt: 0, pb: 0 }} size="small" />}
							label={<Box fontSize={12}>{BuildJobTypeName.BC}</Box>}
						/>
					</Box>
					<Box display="flex" sx={{ ml: 2, mt: 0 }}>
						<FormControlLabel
							key={BuildJobType.Release}
							value={BuildJobType.Release}
							control={<Radio sx={{ pt: 0, pb: 0 }} size="small" />}
							label={<Box fontSize={12}>{BuildJobTypeName.Release}</Box>}
							sx={{ mt: 0 }}
						/>
						<Select
							value={releaseVersion}
							onChange={(e) => handleReleaseVersionChange(e)}
							sx={{ mt: 0, height: 25, width: 115, fontSize: 12 }}
						>
							{releaseUrls.map((build) => (
								<MenuItem
									sx={{ fontSize: 12 }}
									key={build.name}
									value={build.name}
								>
									{build.name}
								</MenuItem>
							))}
						</Select>
					</Box>
				</RadioGroup>
			</FormControl>
		</Box>
	);
};

export default Jenkin;
